$text-h1: 56px;
$text-h2: 48px;
$text-h3: 32px;
$text-h4: 24px;
$text-p: 18px;

$gold: #c09240;
$coral: #e3c180;
$royal: #29506f;
$coal: #2b2b2b;
$smoke: #e6e7e2;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  height: 100vh;
  width: 100%;

  // cursor: none;
  overflow: hidden;
}

.cursor {
  height: 20px;
  width: 20px;
  border: 3px solid $smoke;
  border-radius: 50%;
  position: absolute;

  display: grid;
  place-items: center;

  transition: 200ms ease-out;
  animation: cursor-animation 2s ease-in infinite;

  &::before {
    content: "";
    height: 20px;
    width: 20px;
    position: absolute;

    border: 5px solid $coral;
    opacity: 0.3;
    border-radius: 50%;
    top: -8px;
    left: -8px;
  }
}

.scroll {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);

  height: 60vh;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;

  color: $smoke;

  font-size: $text-p;

  .divider {
    height: 10vh;
    width: 2px;
    border-radius: 2rem;
    background-color: $smoke;
    opacity: 0.7;
  }

  .index-box {
    width: 100%;
    margin: 10px;
    padding: 5px;

    text-align: center;
    user-select: none;
    opacity: 0.5;

    transition: 200ms ease-in;

    &:hover {
      opacity: 1;
    }
  }
}

.intro {
  height: 30vh;
  width: 40%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  user-select: none;

  color: $gold;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .glitch {
    height: 100%;
    width: 100%;
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h1 {
      font-size: $text-h1;
      color: $gold;
    }
    p {
      font-size: $text-p;
      color: $coral;
    }

    &::before {
      align-self: flex-end;
      justify-self: flex-start;
      background-color: red;

      height: 100px;
      width: 100px;
      border-radius: 50%;
    }
    &::after {
      align-self: flex-start;
      justify-self: flex-end;
      background-color: red;

      height: 100px;
      width: 100px;
      border-radius: 50%;
    }
  }
}

@keyframes cursor-animation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
